.aside{
    @include tablet{
        margin-top: -100vh;
        margin-left: 170vh;
        position: sticky;
        top: 0;
    }
    position: fixed;
    display: grid;
    grid-template-rows: repeat(3 , 1fr);
    justify-items: center;
    right: 1rem;
    z-index: 2;
    @include tablet{
        right: 7rem; 
    }

    .lineatop,
    .lineabottom{
        border-left: .3rem solid $mostazaOscuro;
        height: 100%;
    }
    .redes{
        ul{
            li{
                a{
                    img{
                        width: 2rem;
                        height: 2rem;
                        margin: 1rem 0;
                        background-color: $Trans;
                        border-radius: 25%;
                    }
                }
            }
        }
    }
}